import Router from 'vue-router'
import Login from '@/components/Login'

import Container from '@/components/Container'
import AnnoPage from '@/components/AnnoPage'
import AnnotationSentenceEvaluate from '@/components/AnnotationSentenceEvaluate'
import AnnotationSentenceOperate from '@/components/AnnotationSentenceOperate'
import AnnoToUserPage from '@/components/AnnoToUserPage'
import UpdateUserAccountPassword from '@/components/UpdateUserAccountPassword'
import UserAccount from '@/components/UserAccount'
import AnnotationTypeMaintain from '@/components/AnnotationTypeMaintain'

export default new Router({
  mode:'history',
  routes: [
    {
      path:'/',
      name:'用户管理',
      component:Container,
      // leaf:true,
      roles:['管理员'],
      children:[
        {path:'/uiAnnoToUserPage',roles:['管理员'],component:AnnoToUserPage,name:'管理标注'},
        {path:'/uiUserAccount',roles:['管理员'],component:UserAccount,name:'用户维护'}
        // {path:'/uiUpdateUserPassword',roles:['标注人员','审核人员'],component:UpdateUserAccountPassword,
        //   name:'修改密码'}
      ]
    },
    {
      path:'/',
      name:'标注管理',
      component:Container,
      roles:['标注人员','审核人员','管理员'],
      children:[
        {path:'/uiAnnotationSentenceOperate',roles:['标注人员','管理员'],component:AnnotationSentenceOperate,name:'标注'},
        // {path:'/uiAnnoPage',roles:['标注人员','审核人员','管理员'],component:AnnoPage,name:'标注'},
        {path:'/uiAnnotationSentenceEvaluate',roles:['审核人员','管理员'],component:AnnotationSentenceEvaluate,name:'审核标注'}
      ]
    },
    // {
    //   path:'/',
    //   name:'配置管理',
    //   component:Container,
    //   roles:['管理员'],
    //   children:[
    //     {path:'/uiAnnotationTypeMaintain',roles:['管理员'],component:AnnotationTypeMaintain,name:'类型维护'}
    //   ]
    // },
    {
      path:'/uiLogin',
      name:'Login',
      component:Login,
      hidden:true
    },
    {
      path:'*',
      hidden:true,
      redirect:{path:'/uiLogin'}
    }
  ]
})



