import myFetch from '@/utils/myFetch'

export function queryUserAccount(){
	var param=JSON.stringify({
	});
	return myFetch(
		{
			method:'post',
			url:'/user/queryUserAccount.do',
			data:param
		}
		);
}
export function addUserAccount(pUsername,pPassword,pRole){
	var param=JSON.stringify({
		'accountName':pUsername,
		'password':pPassword,
		'role':pRole
	});
	return myFetch(
		{
			method:'post',
			url:'/user/addUserAccount.do',
			data:param
		}
		);
}
export function updateUserAccountPassword(pPassword,pId){
	var param=JSON.stringify({
		'newPassword':pPassword,
		'userId':pId
	});
	return myFetch(
		{
			method:'post',
			url:'/user/updateUserAccountPassword.do',
			data:param
		}
		);
}
export function setUserAccountState(pState,pId){
	var param=JSON.stringify({
		'currentState':pState,
		'userId':pId
	});
	return myFetch(
		{
			method:'post',
			url:'/user/setUserAccountState.do',
			data:param
		}
		);
}





