<template>
  <div>
    <modal-term-detail ref='termDialog' v-bind:annotation='selectedTerm' v-bind:submit='submitTerm'/>
    <modal-delete-term ref='termDeleteDialog' v-bind:annotation='selectedTerm' 
    v-bind:submit='submitDeleteTerm'/>
        <el-row style="background-color:#F2F2F2;margin-top:10px;margin-bottom:5px;">
            <el-col :span="2">
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="24">
              <div id="mark-result">
                <el-table
                  v-loading="loading"
                  element-loading-text="加载数据"
                  :data="bratDatas"
                  border
                  style="width:100%"  
                  empty-text="暂无数据"
                  highlight-current-row
                  >
                  <el-table-column label="ANID"  min-width="100"  header-align="center">
                    <template slot-scope="brats">
                        <span>{{brats.row.id}}</span>
                    </template>
                  </el-table-column>
                  <el-table-column label="标注状态" min-width="100" header-align="center">
                    <template slot-scope="brats">
                      <span>{{brats.row.state}}</span>
                    </template>
                  </el-table-column>
                  <el-table-column label="标注数据"  min-width="800" header-align="center" >
                    <template slot-scope="brats" >
                      <malgo-brat v-bind:brat-data='brats.row.bratData' 
                        :brat-config-data='bratConfig' 
                        :brat-id='brats.row.id'
                        :on-select-text='selectText'
                        :on-select-label='selectLabel'>
                      </malgo-brat>
                    </template>
                  </el-table-column>
                </el-table>       
              </div>
              <el-pagination 
                class="pagination-container" @size-change="handleSizeChange" 
                @current-change="handleCurrentChange"
                :current-page.sync="pageIndex" :page-sizes="[3,4,6,8]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalLimit">
                </el-pagination>
            </el-col>
        </el-row>
  </div>
</template>
<script type="text/javascript">
    import MalgoBrat from '@/components/MalgoBrat.vue'
    import ModalTermDetail from '@/components/ModalTermDetail.vue'
    import ModalDeleteTerm from '@/components/ModalDeleteTerm.vue'
    import {config,getAnnotationList,addAnnotationSentence,deleteAnnotationSentence} from '@/api/annotation'
    export default {
      data () {
        return {
          pageIndex:1,
          totalPages:0,
          totalLimit:0,
          pageSize:10,
          textarea:null,
          bratConfig:null,
          bratDatas:[],
          currentBratId:null,
          selectedTerm: {
            term: null,
            types: [{label:'句末',value:'Sentence-end'}],
            // isAddTerm: false,
            startOffset: null,
            endOffset: null,
            type: null,
            tag:null
          },
          loading:false,
          depth:0,
          prefix:'| ---------------------------------------------------------------'
        }
      },
      created () {
        var self=this
        self.refreshData()
      },
      methods:{
        handleSizeChange(val){
          var self=this;
          self.pageSize=val;
          self.refreshData();
        },
        handleCurrentChange(val){
          var self=this;
          self.pageIndex=val;
          self.refreshData();
        },
        //双击注解,然后弹出modal,modal上的按钮事件
        submitDeleteTerm(){
          var self=this;
          deleteAnnotationSentence(self.currentBratId,self.selectedTerm.tag)
          .then(function(response){
            // for(var i=0;i<self.bratDatas.length ;i++){
            //   if(self.bratDatas[i].id==self.currentBratId){
            //     self.bratDatas.splice(i,1,response.data.bratData)
            //     self.bratDatas[i].bratData=response.data.bratData
            // }
            if(response.service_success===true){
              self.refreshData()  
            }
          });
        },
        //在父组件中定义该方法通过v-bind进行key/value的绑定，供子组件回调该方法
        submitTerm () {
          var self=this;
          addAnnotationSentence(self.selectedTerm.term,self.selectedTerm.type,self.selectedTerm.startOffset,
            self.selectedTerm.endOffset,self.currentBratId)
          .then(function(response){
            console.log(response)
            // for(var i=0;i<self.bratDatas.length ;i++){
            //   if(self.bratDatas[i].id==self.currentBratId){
            //     self.bratDatas[i].bratData=response.data.bratData;
            //   }
            // }
            if(response.service_success===true){
              self.refreshData()  
            }
            
          });
        },
        //在父组件中定义的方法，并注入到子组件中。供子组件调用，传入相应的参数，并带参数打开modal组件
        selectText (data, callback) {
          var self=this;
          self.selectedTerm.term=data.term;
          self.selectedTerm.startOffset=data.startOffset;
          self.selectedTerm.endOffset=data.endOffset;
          self.currentBratId=data.currentBratId;
          self.$refs.termDialog.open(self.selectedTerm);
        },
        selectLabel (data) {
          var self=this;
          self.selectedTerm.term=data.term;
          self.selectedTerm.type=data.type;
          self.currentBratId=data.currentBratId;
          self.selectedTerm.tag=data.tag;
          //加一个判断，如果type类型后缀为"-unconfirmed",为自动标注,不允许弹出框
          if(!self.selectedTerm.type.endsWith('-unconfirmed')){
            self.$refs.termDeleteDialog.open(self.selectedTerm);
          }
        },
        refreshData () {
          var self=this;
          self.bratDatas=[];
          if(self.bratConfig==null){
            config()
            .then((response)=>{
              console.log(response)
              self.bratConfig=response
            })
          }
          getAnnotationList(self.pageIndex,self.pageSize,['已分配','已标注'])
          .then(function(response){
            console.log('查询数据')
            console.log(response)
            self.bratDatas=response.data.dataList;
            self.totalLimit=response.data.total;
            self.loading=false;
          })
        }
      },
      components:{
        'malgo-brat':MalgoBrat,
        'modal-term-detail':ModalTermDetail,
        'modal-delete-term':ModalDeleteTerm
      }
    }
</script>


