<template>
      <!-- overflow-x:scroll;white-space:nowrap; overflow-y:hidden-->
      <!-- style="overflow-x:scroll;white-space:nowrap;width:100%;height:600px" -->
    <div v-bind:id="'brat-result-'+bratId"></div>
</template>
<script>
  export default {
    props: ['bratData', 'bratConfigData', 'bratId','bratState', 'onSelectText', 'onSelectLabel','onUpdateLabel'],
    data () {
      return {
        dispatcher:null,
        webFontURLs: [
          'static/brat/fonts/Astloch-Bold.ttf',
          'static/brat/fonts/PT_Sans-Caption-Web-Regular.ttf',
          'static/brat/fonts/Liberation_Sans-Regular.ttf'
        ],
        tspanLengths:[]        
      }
    },
    created () {

    },
    mounted () {
      return this.marking();
    },
    methods: {
      refresh(){
      },
      //在指定位置画出标注数据,并且绑定鼠标事件
      marking () { 
      // if(this.bratData!=null){  
          if(!$('#brat-result-'+this.bratId).hasClass('hasSVG')){
            this.dispatcher=Util.embed('brat-result-' + this.bratId, this.bratConfigData, this.bratData,this.webFontURLs)
          }else{
            // console.log('刷新')
            // this.dispatcher.post('requestRenderData', [this.bratData]);
          } 
          if(this.dispatcher!=null){
            this.dispatcher.on('doneRendering', this.bindMouseOperation);
          }
        // }
      },
      //鼠标事件操作细节
      bindMouseOperation () {
        var self = this;
        var tspans = $('#brat-result-'+self.bratId+' tspan[data-chunk-id]');
        var offset = 0;
        self.tspanLengths=[];
        tspans.each(function (i) {
          self.tspanLengths.push(offset);
          var html = $(this).html();
          html = html.replace(/&nbsp;/g, ' ');
          html = html.replace(/&lt;/g, '<');
          html = html.replace(/&gt;/g, '>');
          offset += html.length;
        });
        //将鼠标事件绑定到对应的bratId模块
        $('#brat-result-'+self.bratId+' .text').off('mouseup');
        $('#brat-result-'+self.bratId+' .text').mouseup(function(e){
          var selection = document.getSelection();
          var startNode = $(selection.baseNode).parent();
          var endNode = $(selection.extentNode).parent();
          var startToken = startNode.attr('data-chunk-id');
          var endToken = endNode.attr('data-chunk-id');
          // var labelNode=$(startNode).parent().parent().next().children();
          var testNodeArr=$(startNode).parent().parent().siblings();
          var labelNode=[];
          var tagArr=[];
          for(var i=0;i<testNodeArr.length;i++){
            console.log($(testNodeArr[i]).attr('transform'))
            if($(testNodeArr[i]).attr('transform')!=null&&$(testNodeArr[i]).attr('transform').indexOf('translate')!=-1){
              //说明当前节点是我们要的labelNode节点
              //可以根据startToken和endToken去获取对应文本的类型
              for(var j=0;j<testNodeArr[i].childNodes.length;j++){
                 labelNode.push(testNodeArr[i].childNodes[j]); 
              }
            }
          }
          if(labelNode){
            for(var k=parseInt(startToken);k<=parseInt(endToken);k++){
              if(labelNode[parseInt(k)+1].childNodes[1]){
                var currentTag=$(labelNode[parseInt(k)+1].childNodes[1].childNodes[0]).attr('data-span-id');
                tagArr.push(currentTag);
              }
            }
          }
          //只有选择的内容在一句句子中时才响应操作
          if($(selection.baseNode).parent().parent().is($(selection.extentNode).parent().parent())){
            //只有当选中了词之后才响应 
            if(selection.extentOffset != selection.baseOffset || startToken != endToken){
              var startOffset = 0;
              var endOffset = 0;
              if(parseInt(startToken) < parseInt(endToken)){
                startOffset = self.tspanLengths[startToken] + selection.baseOffset;
                endOffset = self.tspanLengths[endToken] + selection.extentOffset;
              }else if(startToken == endToken){
                startOffset = self.tspanLengths[startToken] + Math.min(selection.baseOffset, selection.extentOffset);
                endOffset = self.tspanLengths[endToken] + Math.max(selection.baseOffset, selection.extentOffset);
              }else{
                startOffset = self.tspanLengths[endToken] + selection.extentOffset;
                endOffset = self.tspanLengths[startToken] + selection.baseOffset;
              }
              var word = self.bratData.text.substr(startOffset, endOffset - startOffset);
              var spaceOffset = 0;
              for(var i = 0; i < word.length; i++){
                if(word[i] == ' '){
                  spaceOffset++;
                }else{
                  break;
                }
              }
              word = word.substr(spaceOffset);
              startOffset += spaceOffset;
              // 组织弹出框数据 TODO
              var entityList=[]
              if(tagArr.length>0){
                for(var j in tagArr){
                  for(var i in self.bratData.entities){
                    if(tagArr[j]==self.bratData.entities[i][0]){
                      var value=self.bratData.entities[i]
                      var key=tagArr[j]
                      var text = self.bratData.text.substr(value[2][0][0], value[2][0][1] - value[2][0][0]);
                      entityList.push({key:key,type:value[1],term:text})
                    }
                  }
                }
              }
              var selectedTerm = {
                term: word,
                startOffset: startOffset,
                endOffset: endOffset,
                currentBratId:self.bratId,
                currentBratState:self.bratState,
                combineAnno:entityList,
                sentenceOffset:endToken
              }
              // console.log(selectedTerm)
              self.onSelectText(selectedTerm, self.marking)
            }
          }
          e.stopPropagation();         
        });
        var timer = null
        // 双击标签事件
        $('#brat-result-'+self.bratId+' .span rect').off('dblclick');
        $('#brat-result-'+self.bratId+' .span rect').dblclick(function(e){
            clearTimeout(timer)
            var tag = $(this).attr('data-span-id');
            var entity = null;
            for(var i in self.bratData.entities){
              if(self.bratData.entities[i][0] == tag){
                  entity = self.bratData.entities[i];
                  break;
              }
            }
            var text = self.bratData.text.substr(entity[2][0][0], entity[2][0][1] - entity[2][0][0]);
            var selectedTerm={
              term:text,
              type:entity[1],
              currentBratId:self.bratId,
              currentBratState:self.bratState,
              tag:tag
            }
            self.onSelectLabel(selectedTerm);
            e.stopPropagation();
        });
        //单击标签事件
        $('#brat-result-'+self.bratId+' .span rect').off('click');
        $('#brat-result-'+self.bratId+' .span rect').click(function(e){
            clearTimeout(timer)
            timer=setTimeout(()=>{
            var tag = $(this).attr('data-span-id');
            var entity = null;
            for(var i in self.bratData.entities){
              if(self.bratData.entities[i][0] == tag){
                  entity = self.bratData.entities[i];
                  break;
              }
            }
            var text = self.bratData.text.substr(entity[2][0][0], entity[2][0][1] - entity[2][0][0]);
            var selectedTerm={
              term:text,
              type:entity[1],
              currentBratId:self.bratId,
              currentBratState:self.bratState,
              tag:tag
            }
            self.onUpdateLabel(selectedTerm);
            e.stopPropagation();
          },300)
        });
      }
    }
  }
</script>


