<template>
  <div>
        <el-row style="background-color:#F2F2F2;margin-top:10px;margin-bottom:5px;">
            <el-col :span="4">
                <el-button type="success" @click='batchExamine'>批量审核</el-button>
            </el-col>
            <el-col :span="4">
              <el-select v-model="condition.user"  placeholder="请选择用户" clearable @change="enterClick">
                  <el-option
                    v-for="item in condition.users"
                    :key="item.id"
                    :label="item.accountName"
                    :value="item.id">
                  </el-option>
                </el-select>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="24">
              <div id="mark-result">
                <el-table
                  ref="multipleTable"
                  v-loading="loading"
                  element-loading-text="加载数据"
                  :data="bratDatas"
                  style="width:100%"  
                  empty-text="暂无数据"
                  highlight-current-row
                  @select="tableSelect"
                  @selection-change="handleSelectionChange"
                  >
                   <el-table-column
                    type="selection"
                    width="55">
                  </el-table-column>
                  <el-table-column type="expand">
                    <template slot-scope="props">
                      <single-annotation-sentence-operate 
                      v-bind:bratConfig="bratConfig"
                      v-bind:bratDataObj="props.row"
                      v-bind:showBtn="false">
                      </single-annotation-sentence-operate>
                    </template>
                  </el-table-column>  
                  <el-table-column label="ANID"  min-width="80"  header-align="center">
                    <template slot-scope="brats">
                        <span>{{brats.row.id}}</span>
                    </template>
                  </el-table-column>
                  <el-table-column label="标注状态" min-width="100" header-align="center">
                    <template slot-scope="brats">
                      <span>{{brats.row.state}}</span>
                    </template>
                  </el-table-column>
                  <el-table-column label="标注数据"  min-width="500" header-align="center" >
                    <template slot-scope="brats">
                      {{brats.row.bratData.text.substring(0,40)}}
                    </template>
                  </el-table-column>
                  <!-- <el-table-column label="标注数据"  min-width="500" header-align="center" >
                    <template slot-scope="brats">
                      <malgo-brat v-bind:brat-data='brats.row.bratData' 
                        :brat-config-data='bratConfig' 
                        :brat-id='brats.row.id'>
                      </malgo-brat>
                    </template>
                  </el-table-column> -->
                  <el-table-column label="更新时间" min-width="120" header-align="center">
                    <template slot-scope="brats">
                      {{brats.row.gmtModified}}
                    </template>
                  </el-table-column>
                   <el-table-column label="操作"  min-width="180"
                    header-align="center">
                    <template  slot-scope="scope">
                      <el-button type="primary" size="small" icon="circle-check" :disabled="scope.row.state==='已审核'?true:false"
                                 @click='commitAnnotation(scope.row)'>审核提交</el-button>
                    </template>
                  </el-table-column>       
                </el-table>
              </div>
              <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page.sync="pageIndex" :page-sizes="[2,3,5,8]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalLimit">
              </el-pagination>
            </el-col>
        </el-row>
  </div>
</template>
<script type="text/javascript">
    import MalgoBrat from '@/components/MalgoBrat.vue'
    import SingleAnnotationSentenceOperate from '@/components/SingleAnnotationSentenceOperate.vue'
    import {config,
      queryForDistributionAnnotation,finishAnnotationSentence} from '@/api/annotation'
    import {queryUserAccount} from '@/api/account'      
    export default {
      data () {
        return {
          pageIndex:1,
          totalLimit:0,
          pageSize:10,
          textarea:null,
          bratConfig:null,
          bratDatas:null,
          loading:false,
          condition:{
            state:null,
            termText:null,
            users:[],
            user:'',
            states:[]
          },
          multipleSelection:[],
          userVisible:false,
          user:{
            account:null
          }
        }
      },
      created () {
        var self=this
        self.initSelect()
      },
      methods:{
        commitAnnotation(currentRow){
          var self=this
          console.log(currentRow)
          var idArr=[]
          idArr.push(currentRow.id)
          finishAnnotationSentence(idArr)
            .then((response)=>{
              if(response.service_success==true){
                self.$message({
                  message:'审核成功！',
                  type:'success'
                })
                self.refreshData()
              }
            })  
        },
        batchExamine(){
          var self=this
          if(self.multipleSelection.length>0){
            var idArr=[]
            Object.keys(self.multipleSelection).forEach((key)=>{
              idArr.push(self.multipleSelection[key].id)
            })
            self.loading=true
            finishAnnotationSentence(idArr)
            .then((response)=>{
              if(response.service_success==true){
                self.$message({
                  message:'批量审核成功！',
                  type:'success'
                })
                self.refreshData()
              }
              self.loading=false
            })
          }else{
            self.$message.error('请选取要审核的标注')
          }
        },
        tableSelect(selection,currentRow){
          var self=this
          console.log(selection)
          if(currentRow.state==='已审核'){
            self.$message({
              message:'当前记录已审核，请选择其它记录！',
              type:'warning'
            })
            // self.$refs.multipleTable.setCurrentRow()
            self.$refs.multipleTable.clearSelection()
          }

        },
        handleSelectionChange(val){
          var self=this
          self.multipleSelection = val
          if(self.multipleSelection.length>0){
          }
        },
        initSelect(){
          var self=this
          queryUserAccount()
          .then((response)=>{
            Object.keys(response.data).forEach((key)=>{
              if(response.data[key].role==='标注人员')
                  self.condition.users.push(response.data[key])    
            })
          })
        },
        enterClick(){
          var self=this
          if(self.bratConfig==null){
            self.loading=true
            config()
            .then(function(response){
              self.bratConfig=response
              self.loading=false
              self.refreshData()
            }) 
          }else{
            self.refreshData()
          }
        },
        handleSizeChange(val){
          var self=this;
          self.pageSize=val;
          self.refreshData();
        },
        handleCurrentChange(val){
          var self=this;
          self.pageIndex=val;
          self.refreshData();
        },
        refreshData () {
          var self=this;
          self.bratDatas=[];
          self.loading=true
          var stateArr=[]
          // stateArr.push('已标注')
          queryForDistributionAnnotation(self.pageIndex,self.pageSize,stateArr,self.condition.user)
          .then(function(response){
            self.bratDatas=response.data.dataList;
            self.totalLimit=response.data.total;
            console.log(response)
            self.loading=false;
          })
        }
      },
      components:{
        'malgo-brat':MalgoBrat,
        'single-annotation-sentence-operate':SingleAnnotationSentenceOperate
      }
    }
</script>


