<template>
	<div>
		<el-row style="background-color:#F2F2F2;margin-top:10px;margin-bottom:5px;">
	        <el-col :span="2">
	            <el-button type="success" @click='addAccount'>新增用户</el-button>
	        </el-col>
		</el-row>
	    <el-row>
	        <el-col :span="24">
	            <el-table
	              v-loading="loading"
	              element-loading-text="加载数据"
	              :data="accountArr"
	              style="width:100%"  
	              empty-text="暂无数据"
	              highlight-current-row
	              >
	              <el-table-column label="账户名称"  min-width="100"  header-align="center">
	                <template slot-scope="scope">
	                    <span>{{scope.row.accountName}}</span>
	                </template>
	              </el-table-column>
	              <el-table-column label="密码" min-width="120" header-align="center">
	                <template slot-scope="scope">
	                    <span>{{scope.row.password}}</span>
	                </template>
	              </el-table-column>
	              <el-table-column label="角色" min-width="100">
	                <template slot-scope="scope">
	                  <span>{{scope.row.role}}</span>
	                </template>
	              </el-table-column>
	              <el-table-column label="状态" min-width="100">
	                <template slot-scope="scope">
	                  <span v-if="scope.row.state==='enable'">激活</span>
	                  <span v-else>冻结</span>
	                </template>
	              </el-table-column>
	              <el-table-column label="新增时间" min-width="120" header-align="center">
	                <template slot-scope="scope">
	                  {{scope.row.gmtModified}}
	                </template>
	              </el-table-column>
	              <el-table-column label="操作" min-width="150" header-align="center">
	              	<template  slot-scope="scope">
	              	 <el-button-group>	
                      <el-button type="primary" size="small" :disabled="scope.row.id===1?true:false"
                                 @click='handleUpdateUserPassword(scope.row)'>修改用户密码</el-button>
                      <el-button type="success" size="small"
                      			:disabled="scope.row.id===1?true:false"
                                 @click='handleSetUserState(scope.row)'>设定用户状态</el-button>
                      </el-button-group>
                    </template>
	              </el-table-column>       
	            </el-table>
	        </el-col>
	    </el-row>
	    <el-dialog title="新增用户" :visible.sync='userVisible' size="tiny">
          <el-form :model="user" label-position="left" label-width="110px">
          	<el-form-item label="账号名称">
          		<el-input v-model="user.accountName"></el-input>
          	</el-form-item>
          	<el-form-item label="用户密码">
          		<el-input v-model="user.password"></el-input>
          	</el-form-item>
            <el-form-item label="用户角色">
              <el-select v-model="user.role" 
                placeholder="请选择" style=" width:100%">
                <el-option
                  v-for="item in user.roles"
                  :key="item.id"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>       
            <el-form-item>
              <el-button type="primary" @click="confirmAdd">确认</el-button>
              <el-button @click='userVisible=false'>关闭</el-button>
           </el-form-item>
          </el-form>
        </el-dialog>
        <el-dialog title="重置用户密码" :visible.sync='resetUserPasswordVisible' size="tiny">
        	<el-form :model="userAccount">
		        <el-form-item label="用户名称">
		          <el-input v-model="userAccount.accountName" :readonly="true"></el-input>
		        </el-form-item>
		        <el-form-item label="录入新密码">
		          <el-input type="password" auto-complete="off" placeholder="密码" v-model="userAccount.newPassword"></el-input>
		        </el-form-item>
		        <el-form-item>
		          <el-button type="info" @click="submitChange">确认修改</el-button>
		        </el-form-item>
		    </el-form>
        </el-dialog>
        <el-dialog title="设置用户状态" :visible.sync='resetUserStateVisible' size="tiny" @close="confirmUserAccountState" style="text-align:center">
        	<el-switch
			  v-model="currentUserAccountState"
			  on-color="#13ce66"
			  off-color="#ff4949"
			  on-text="激活"
  			  off-text="冻结"
  			  on-value="enable"
    		  off-value="disable">
			</el-switch>
        </el-dialog>
	</div>
</template>
<script>
	import {queryUserAccount,addUserAccount,updateUserAccountPassword,setUserAccountState} from '@/api/account'
    export default {
      data () {
        return {
          accountArr:[],
          loading:false,
          pageIndex:1,
          pageSize:10,
          totalLimit:0,
          userVisible:false,
          user:{
          	accountName:'',
          	password:'',
          	role:'',
          	roles:[
          		{id:1,value:'审核人员',label:'审核人员'},
          		{id:2,value:'标注人员',label:'标注人员'}
          	]
          },
          resetUserPasswordVisible:false,
          userAccount:{
          	accountName:'',
          	newPassword:'',
          	id:''
          },
          resetUserStateVisible:false,
          currentUserAccountState:'enable'
        };
      },
      created(){
      	var self=this
      	self.initTable()
      },
      methods: {
      	confirmUserAccountState(){
      		var self=this
      		setUserAccountState(self.currentUserAccountState,self.userAccount.id)
      		.then((response)=>{
      			if(response.service_success===true){
      				self.$message({
		              message:'成功设定状态！',
		              type:'success'
		            })
		            self.initTable()
      			}else{
      				self.$message({
		              message:response.msg,
		              type:'error'
		            })
      			}
      		})
      	},
      	submitChange(){
      		var self=this
      		updateUserAccountPassword(self.userAccount.newPassword,self.userAccount.id)
      		.then((response)=>{
      			if(response.service_success===true){
      				self.$message({
		              message:'成功重置密码！',
		              type:'success'
		            })
		            self.initTable()
      			}else{
      				self.$message({
		              message:response.msg,
		              type:'error'
		            })
      			}
      		})
      	},
      	handleUpdateUserPassword(currentRow){
      		var self=this
      		self.resetUserPasswordVisible=true
      		self.userAccount=currentRow
      	},
      	handleSetUserState(currentRow){
      		var self=this
      		self.resetUserStateVisible=true
      		self.userAccount=currentRow
      	},
      	confirmAdd(){
      		var self=this
      		addUserAccount(self.user.accountName,self.user.password,self.user.role)
      		.then((response)=>{
      			console.log(response)
      			if(response.service_success===true){
      				self.$message({
		              message:'新增成功！',
		              type:'success'
		            })
		            self.initTable()
      			}else{
      				self.$message({
		              message:response.msg,
		              type:'error'
		            })
      			}
      			self.userVisible=false
      		})
      	},
      	addAccount(){
      		var self=this
      		self.userVisible=true
      		self.user.accountName=''
      		self.user.password=''
      		self.user.role=''
      	},
      	initTable(){
      		var self=this
      		queryUserAccount()
      		.then((response)=>{
      			console.log(response)
      			self.accountArr=response.data
      		})
      	},
      	handleCurrentChange(val){
      		var self=this
      		self.pageIndex=val
      	},
      	handleSizeChange(val){
      		var self=this
      		self.pageSize=val
      	}
      }
    }
</script>


