<template>
  <div>
    <single-annotation-sentence-operate
      v-if="hackReset" 
      v-bind:bratConfig="bratConfig"
      v-bind:bratDataObj="bratDataObj"
      v-bind:showBtn="true"
      v-bind:popBratData="popBratData">
    </single-annotation-sentence-operate>
  </div>
</template>
<script type="text/javascript">
    import {config,getAnnotationList} from '@/api/annotation'
    import SingleAnnotationSentenceOperate from '@/components/SingleAnnotationSentenceOperate.vue'
    export default {
      data () {
        return {
          bratDataObj:null,
          bratConfig:null,
          pageIndex:1,
          pageSize:1,
          hackReset:true
        }
      },
      created () {
        var self=this
        console.log('父组件渲染！')
        self.queryBratConfig()
      },
      methods:{
        queryBratConfig(){
          var self=this
          if(self.bratConfig==null){
            config()
            .then((response)=>{
              self.bratConfig=response
              self.popBratData()
            })
          }else{
            self.popBratData()
          }
        },
        popBratData() {
          var self=this
          var bratDatas=[]
          self.hackReset=false
          getAnnotationList(self.pageIndex,self.pageSize,['已分配','标注中'])
          .then(function(response){
            self.hackReset=true
            bratDatas=response.data.dataList;
            // if(bratDatas.length>0)
            self.bratDataObj=bratDatas[0]
            console.log(self.bratDataObj)
          })
        }
      },
      components:{
        'single-annotation-sentence-operate':SingleAnnotationSentenceOperate
      }
    }
</script>


