import myFetch from '@/utils/myFetch'
export function login(username,password){
	var param=JSON.stringify({
		'accountName':username,
		'password':password
	});
	return myFetch(
		{
			method:'post',
			url:'/user/anonymous/login.do',
			data:param
		}
		);
}


