<template>
    <div>
      <el-row>
        <el-col :span='24'>
        <el-form :model="userAccount" label-width="100px" style="text-align:center;margin-top:20px">
          <el-form-item label="用户名称">
            <el-input v-model="userAccount.accountName" :readonly="true"></el-input>
          </el-form-item>
          <el-form-item label="录入新密码">
            <el-input type="password" auto-complete="off" placeholder="密码" v-model="userAccount.newPassword"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="info" @click="mySubmit">确认修改</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
    </div>
</template>
<script>
    import {queryUserAccount,updateUserAccountPassword} from '@/api/account'
    export default {
      data () {
        return {
          userAccount:{
            accountName:JSON.parse(sessionStorage.getItem('userInfo')).accountName,
            id:JSON.parse(sessionStorage.getItem('userInfo')).id
          }
        };
      },
      methods: {
        mySubmit(){
          var self=this
          updateUserAccountPassword(self.userAccount.newPassword,self.userAccount.id)
          .then((response)=>{
            if(response.service_success===true){
              self.$message({
                  message:'成功重置密码，请重新登录！',
                  type:'success'
                })
              self.$router.push({ path: '/uiLogin' });
            }else{
              self.$message({
                  message:response.msg,
                  type:'error'
                })
            }
          })
        }
      }
    }
</script>


