<template>
  <div>
    <modal-term-detail ref='termDialog' v-bind:annotation='selectedTerm' v-bind:submit='submitTerm'/>
    <modal-delete-term ref='termDeleteDialog' v-bind:annotation='selectedTerm' 
    v-bind:submit='submitDeleteTerm'/>

    <el-form v-if="bratDataObj!=null" :model="bratDataObj"  :inline="true" class="demo-form-inline" style="text-align:center;font-size:48px;font-weight:bold;">
      <el-form-item label="标注序列">
        <el-input v-model="bratDataObj.id" style="margin-top:10px" :readonly="true"></el-input>
      </el-form-item>
      <el-form-item label="标注状态">
        <el-input v-model="bratDataObj.state" style="margin-top:10px" :readonly="true"></el-input>
      </el-form-item>
      <el-form-item label="最近更新时间">
        <el-input v-model="bratDataObj.gmtModified" style="margin-top:10px" :readonly="true"></el-input>
      </el-form-item>
    </el-form>
    <el-card>
      <div slot="header" class="clearfix" style="text-align:center;font-size:22px;">
        <span style="line-height: 36px;">标注数据</span>
        <el-button style="float: right;" type="primary" @click="confirmSubmit" v-show="showBtn">确认提交</el-button>
      </div>
      <div id="mark-result">
          <malgo-brat v-if="hackReset&&bratDataObj!=null" v-bind:brat-data='bratDataObj.bratData'
            :brat-config-data='bratConfig' 
            :brat-id='bratDataObj.id'
            :on-select-text='selectText'
            :on-select-label='selectLabel'>
          </malgo-brat>
      </div>
    </el-card>
  </div>
</template>
<script type="text/javascript">
    import MalgoBrat from '@/components/MalgoBrat.vue'
    import ModalTermDetail from '@/components/ModalTermDetail.vue'
    import ModalDeleteTerm from '@/components/ModalDeleteTerm.vue'
    import {config,getAnnotationList,
            addAnnotationSentence,
            deleteAnnotationSentence,
            commitAnnotationSentence} from '@/api/annotation'

    export default {
      props:['bratDataObj','bratConfig','showBtn','popBratData'],
      data () {
        return {
          currentBratId:null,
          selectedTerm: {
            term: null,
            types: [{label:'句末',value:'Sentence-end'}],
            startOffset: null,
            endOffset: null,
            type: null,
            tag:null,
            sentenceOffset:null
          },
          hackReset:true
          // nativeBratDataObj:null
        }
      },
      computed:{
        bratDataObjInfo(){
          // self.nativeBratDataObj=Object.assign({},self.bratDataObj)
          console.log(self.nativeBratDataObj)
          return self.nativeBratDataObj
        }
      },
      mounted(){
        var self=this

      },
      created () {
        var self=this
        console.log(self.bratDataObj)
      },
      methods:{
        confirmSubmit(){
          var self=this
          console.log(self.bratDataObj)
          if(self.bratDataObj==null)
            return
          if(self.bratDataObj.state!='标注中'){
            self.$message({
                message:'从未标注，无法提交！',
                type: 'warning'
              });
            return
          }
          commitAnnotationSentence(self.bratDataObj.id)
          .then((response)=>{
              if(response.service_success===true){
                self.$message({
                  message:'提交成功！',
                  type: 'success'
                });
                self.popBratData();
              }
          })
        },
        //双击注解,然后弹出modal,modal上的按钮事件
        submitDeleteTerm(){
          var self=this;
          self.hackReset=false
          deleteAnnotationSentence(self.currentBratId,self.selectedTerm.tag)
          .then(function(response){
            if(response.service_success===true){
              self.hackReset=true
              self.bratDataObj=response.data
            }else{
              self.$message({
                message:response.msg,
                type: 'warning'
              });
            }
          });
        },
        //在父组件中定义该方法通过v-bind进行key/value的绑定，供子组件回调该方法
        submitTerm () {
          var self=this;
          self.hackReset=false
          addAnnotationSentence(self.selectedTerm.term,self.selectedTerm.type,self.selectedTerm.startOffset,
            self.selectedTerm.endOffset,self.currentBratId,self.selectedTerm.sentenceOffset)
          .then(function(response){
            console.log(response)
            self.hackReset=true
            if(response.service_success===true){
              self.bratDataObj=response.data
              // self.bratDataObj=Object.assign({},self.bratDataObj,{test:'false'})
              console.log(self.bratDataObj)
            }else{
              self.$message({
                message:response.msg,
                type: 'warning'
              });
            }
          });
        },
        //在父组件中定义的方法，并注入到子组件中。供子组件调用，传入相应的参数，并带参数打开modal组件
        selectText (data, callback) {
          var self=this;
          self.selectedTerm.term=data.term;
          self.selectedTerm.startOffset=data.startOffset;
          self.selectedTerm.endOffset=data.endOffset;
          self.currentBratId=data.currentBratId;
          self.selectedTerm.sentenceOffset=data.sentenceOffset;
          self.$refs.termDialog.open(self.selectedTerm);
        },
        selectLabel (data) {
          var self=this;
          self.selectedTerm.term=data.term;
          self.selectedTerm.type=data.type;
          self.currentBratId=data.currentBratId;
          self.selectedTerm.tag=data.tag;
          //加一个判断，如果type类型后缀为"-unconfirmed",为自动标注,不允许弹出框
          if(!self.selectedTerm.type.endsWith('-unconfirmed')){
            self.$refs.termDeleteDialog.open(self.selectedTerm);
          }
        } 
      },
      components:{
        'malgo-brat':MalgoBrat,
        'modal-term-detail':ModalTermDetail,
        'modal-delete-term':ModalDeleteTerm
      }
    }
</script>


