import myFetch from '@/utils/myFetch'

//annotation-sentence
export function getAnnotationList(pPageIndex,pPageSize,pStateArr){
	var param=JSON.stringify({
		'pageNum':pPageIndex,
		'pageSize':pPageSize,
		'states':pStateArr
	});
	return myFetch({
		method:'post',
		url:'/annotationSentence/queryAnnotationSentence.do',
		data:param
	})
}
export function config(){
	return myFetch({
		method:'get',
		url:'/static/json/config.json'
	});
}
export function addAnnotationSentence(pTerm,pType,pStartOffset,pEndOffset,pAnId,pSentenceOffSet){
	var param=JSON.stringify({
		'term':pTerm,
		'type':pType,
		'startPosition':pStartOffset,
		'endPosition':pEndOffset,
		'id':pAnId,
		'endToken':pSentenceOffSet
	});
	return myFetch({
		method:'post',
		url:'/annotationSentence/addAnnotationSentence.do',
		data:param
	})
}
export function deleteAnnotationSentence(pAnId,pTag){
	var param=JSON.stringify({
		'id':pAnId,
		'tag':pTag
	});
	return myFetch({
		method:'post',
		url:'/annotationSentence/deleteAnnotationSentence.do',
		data:param
	})	
}

