<template>
  <div>
        <el-row style="background-color:#F2F2F2;margin-top:10px;margin-bottom:5px;">
            <el-col :span="4">
                <el-button type="info" @click='batchDistribution'>批量指派</el-button>
            </el-col>
            <el-col :span="4">
              <el-select v-model="condition.state"  placeholder="请选择类型" clearable @change="enterClick">
                  <el-option
                    v-for="item in states"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
            </el-col>
            <el-col :span="4">
              <el-select v-model="condition.user"  placeholder="请选择用户" clearable @change="enterClick">
                  <el-option
                    v-for="item in condition.users"
                    :key="item.id"
                    :label="item.accountName"
                    :value="item.id">
                  </el-option>
                </el-select>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="24">
              <div id="mark-result">
                <el-table
                  ref="multipleTable"
                  v-loading="loading"
                  element-loading-text="加载数据"
                  :data="bratDatas"
                  style="width:100%"  
                  empty-text="暂无数据"
                  highlight-current-row
                  @selection-change="handleSelectionChange"
                  >
                   <el-table-column
                    type="selection"
                    width="55">
                  </el-table-column>
                  <el-table-column type="expand">
                    <template slot-scope="props">
                      <single-annotation-sentence-operate 
                      v-bind:bratConfig="bratConfig"
                      v-bind:bratDataObj="props.row"
                      v-bind:showBtn="false">
                      </single-annotation-sentence-operate>
                    </template>
                  </el-table-column> 
                  <el-table-column label="ANID"  min-width="80"  header-align="center">
                    <template slot-scope="brats">
                        <span>{{brats.row.id}}</span>
                    </template>
                  </el-table-column>
                  <el-table-column label="标注状态" min-width="100" header-align="center">
                    <template slot-scope="brats">
                      <span>{{brats.row.state}}</span>
                    </template>
                  </el-table-column>
                  <el-table-column label="用户ID" min-width="100">
                    <template slot-scope="brats">
                      {{brats.row.userModifier}}
                    </template>
                  </el-table-column>
                  <el-table-column label="标注数据"  min-width="500" header-align="center" >
                    <template slot-scope="brats">
                      {{brats.row.bratData.text.substring(0,40)}}
                    </template>
                  </el-table-column>
                 <!--  <el-table-column label="标注数据"  min-width="500" header-align="center" >
                    <template slot-scope="brats">
                      <malgo-brat v-bind:brat-data='brats.row.bratData' 
                        :brat-config-data='bratConfig' 
                        :brat-id='brats.row.id'>
                      </malgo-brat>
                    </template>
                  </el-table-column> -->
                  <el-table-column label="更新时间" min-width="120" header-align="center">
                    <template slot-scope="brats">
                      {{brats.row.gmtModified}}
                    </template>
                  </el-table-column>       
                </el-table>
              </div>
              <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page.sync="pageIndex" :page-sizes="[2,4,8,10]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalLimit">
              </el-pagination>
            </el-col>
        </el-row>
        <el-dialog title="批量指派标注" :visible.sync='userVisible' size="tiny">
          <el-form :model="user" label-position="left" label-width="110px">
            <el-form-item label="用户列表">
              <el-select v-model="user.account" 
                placeholder="请选择" style=" width:100%">
                <el-option
                  v-for="item in condition.users"
                  :key="item.id"
                  :label="item.accountName"
                  :value="item.id">
                  <span>{{item.accountName}}</span>
                  <span>{{item.role}}</span>
                </el-option>
              </el-select>
            </el-form-item>       
            <el-form-item>
              <el-button type="primary" @click="confirm">确认</el-button>
              <el-button @click='userVisible=false'>关闭</el-button>
           </el-form-item>
          </el-form>
        </el-dialog>
  </div>
</template>
<script type="text/javascript">
    import MalgoBrat from '@/components/MalgoBrat.vue'
    import SingleAnnotationSentenceOperate from '@/components/SingleAnnotationSentenceOperate.vue'
    import {config,
      queryForDistributionAnnotation,distributionAnnotationSentence} from '@/api/annotation'
    import {queryUserAccount} from '@/api/account'      
    export default {
      data () {
        return {
          pageIndex:1,
          totalLimit:0,
          pageSize:4,
          textarea:null,
          bratConfig:null,
          bratDatas:null,
          loading:false,
          states:[
          {
            value:'未分配',
            label:'未分配'
          },
          {
            value:'已分配',
            label:'已分配'
          },
          {
            value:'标注中',
            label:'标注中' 
          },
          {
            value:'已标注',
            label:'已标注'
          },
          {
            value:'已审核',
            label:'已审核'
          }
          ],
          condition:{
            state:null,
            termText:null,
            users:[],
            user:'',
            states:[]
          },
          batchNum:0,
          multipleSelection:[],
          userVisible:false,
          user:{
            account:null
          }
        }
      },
      created () {
        var self=this
        self.initSelect()
      },
      methods:{
        confirm(){
          var self=this
          if(self.multipleSelection.length>0){
            console.log('multipleSelection')
            var idArr=[]
            Object.keys(self.multipleSelection).forEach((key)=>{
              idArr.push(self.multipleSelection[key].id)
            })
            self.loading=true
            distributionAnnotationSentence(idArr,self.user.account)
            .then((response)=>{
              if(response.service_success==true){
                self.$message({
                  message:'批量更新成功！',
                  type:'success'
                })
                self.userVisible=false
                self.refreshData()
              }
              self.loading=false
            })
          }
        },
        handleSelectionChange(val){
          var self=this
          console.log(val)
          self.multipleSelection = val
          if(self.multipleSelection.length>0){
          }
        },
        initSelect(){
          var self=this
          queryUserAccount()
          .then((response)=>{
            console.log('shit man!')
            console.log(response)
            self.condition.users=response.data
          })
        },
        batchDistribution(){
            var self=this
            if(self.multipleSelection.length==0){
              self.$message.error('请选取要分配的标注')
              return
            }
            self.userVisible=true
        },
        enterClick(){
          var self=this
          if(self.bratConfig==null){
            self.loading=true
            config()
            .then(function(response){
              self.bratConfig=response
              self.loading=false
              self.refreshData()
            }) 
          }else{
            self.refreshData()
          }
        },
        handleSizeChange(val){
          var self=this;
          self.pageSize=val;
          self.refreshData();
        },
        handleCurrentChange(val){
          var self=this;
          self.pageIndex=val;
          self.refreshData();
        },
        refreshData () {
          var self=this;
          self.bratDatas=[];
          self.loading=true
          var stateArr=[]
          if(self.condition.state!=null)
              stateArr.push(self.condition.state)

          queryForDistributionAnnotation(self.pageIndex,self.pageSize,stateArr,self.condition.user)
          .then(function(response){
            self.bratDatas=response.data.dataList;
            self.totalLimit=response.data.total;
            self.loading=false;
          })
        }
      },
      components:{
        'malgo-brat':MalgoBrat,
        'single-annotation-sentence-operate':SingleAnnotationSentenceOperate
      }
    }
</script>


